"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Switch } from "@/components/ui/switch"
import { Slider } from "@/components/ui/slider"
import { Textarea } from "@/components/ui/textarea"
import { Calendar } from "@/components/ui/calendar"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { cn } from "@/lib/utils"
import dynamic from "next/dynamic"

interface RouteStats {
  distance: number;
  duration: number;
  elevationGain: number;
}

const Map = dynamic(() => import("@/components/Map"), { 
  ssr: false,
  loading: () => (
    <div className="h-[600px] w-full rounded-lg bg-gray-100 animate-pulse" />
  )
})

interface SearchResult {
  place_name: string;
  center: [number, number];
}

export default function Home() {
  const [activityType, setActivityType] = useState("run")
  const [date, setDate] = useState<Date>()
  const [pace, setPace] = useState([5.5])
  const [speed, setSpeed] = useState([20])
  const [searchQuery, setSearchQuery] = useState("")
  const [searchResults, setSearchResults] = useState<SearchResult[]>([])
  const [routeStats, setRouteStats] = useState<RouteStats>({
    distance: 0,
    duration: 0,
    elevationGain: 0
  })

  // Format duration (minutes) to HH:MM:SS format
  const formatDuration = (minutes: number): string => {
    const hours = Math.floor(minutes / 60)
    const mins = Math.floor(minutes % 60)
    const secs = Math.round((minutes % 1) * 60)
    return `${hours.toString().padStart(2, '0')}:${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }

  // Format distance to one decimal place
  const formatDistance = (km: number): string => {
    return km.toFixed(1)
  }

  // Update duration when pace or speed changes
  const handlePaceChange = (newPace: number[]) => {
    setPace(newPace)
    if (activityType === "run") {
      setRouteStats(prev => ({
        ...prev,
        duration: prev.distance * newPace[0]
      }))
    }
  }

  const handleSpeedChange = (newSpeed: number[]) => {
    setSpeed(newSpeed)
    if (activityType === "bike") {
      setRouteStats(prev => ({
        ...prev,
        duration: prev.distance / newSpeed[0] * 60
      }))
    }
  }

  // Handle route updates from Map component
  const handleRouteUpdate = (coordinates: number[][], stats: RouteStats) => {
    setRouteStats({
      ...stats,
      duration: activityType === "run" ? stats.distance * pace[0] : stats.distance / speed[0] * 60
    })
  }

  return (
    <main className="flex min-h-screen bg-white">
      {/* Map Section */}
      <div className="flex-1 p-4">
        <div className="mb-4">
          <h1 className="text-2xl font-bold mb-2">Draw Your Route</h1>
          <p className="text-gray-600">Search for a location and click on the map to create your route</p>
          <div className="relative mt-4">
            <Input 
              type="text" 
              placeholder="Search for a location..." 
              className="w-full pr-20"
              value={searchQuery}
              onChange={(e) => handleSearch(e.target.value)}
            />
            {searchResults.length > 0 && (
              <ul className="absolute z-10 bg-white border border-gray-300 rounded-md w-full max-h-60 overflow-auto mt-1">
                {searchResults.map((result) => (
                  <li
                    key={result.place_name}
                    className="px-4 py-2 hover:bg-gray-100 cursor-pointer"
                    onClick={() => {
                      handleSearchSelect(result)
                      setSearchQuery(result.place_name)
                      setSearchResults([])
                    }}
                  >
                    {result.place_name}
                  </li>
                ))}
              </ul>
            )}
            <div className="absolute right-0 top-0 h-full flex gap-2 pr-2">
              <Button variant="ghost" size="icon">üîç</Button>
              <Button variant="ghost" size="icon">üìç</Button>
            </div>
          </div>
        </div>
        <Map 
          onRouteUpdate={handleRouteUpdate}
          onSearch={async (query: string) => {
            if (!query) {
              setSearchResults([])
              return
            }
            try {
              const response = await fetch(
                `https://api.mapbox.com/geocoding/v5/mapbox.places/${encodeURIComponent(query)}.json?access_token=${process.env.NEXT_PUBLIC_MAPBOX_TOKEN}&limit=5`
              )
              if (!response.ok) throw new Error('Search failed')
              const data = await response.json()
              setSearchResults(data.features)
            } catch (err) {
              console.error('Search error:', err)
            }
          }}
          onSearchSelect={(result: SearchResult) => {
            setSearchQuery(result.place_name)
            setSearchResults([])
          }}
        />
      </div>

      {/* Details Panel */}
      <Card className="w-[400px] p-6 rounded-none border-l">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-xl font-semibold">{activityType === "run" ? "Run Details" : "Ride Details"}</h2>
          <div className="flex items-center gap-2">
            <span className={cn("text-sm", activityType === "run" ? "text-orange-500" : "text-gray-400")}>Run</span>
            <Switch 
              checked={activityType === "bike"}
              onCheckedChange={(checked) => setActivityType(checked ? "bike" : "run")}
            />
            <span className={cn("text-sm", activityType === "bike" ? "text-orange-500" : "text-gray-400")}>Bike</span>
          </div>
        </div>

        <div className="space-y-6">
          <div>
            <label className="text-sm font-medium">{activityType === "run" ? "Pace Unit" : "Speed Unit"}</label>
            <Select defaultValue={activityType === "run" ? "min/km" : "km/h"}>
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                {activityType === "run" ? (
                  <>
                    <SelectItem value="min/km">min/km</SelectItem>
                    <SelectItem value="km/h">km/h</SelectItem>
                  </>
                ) : (
                  <>
                    <SelectItem value="km/h">km/h</SelectItem>
                    <SelectItem value="mph">mph</SelectItem>
                  </>
                )}
              </SelectContent>
            </Select>
          </div>

          <div>
            <h3 className="text-lg font-semibold mb-4">{activityType === "run" ? "Run Stats" : "Ride Stats"}</h3>
            <div className="grid grid-cols-2 gap-4">
              <div className="text-center">
                <div className="text-3xl font-bold">{formatDistance(routeStats.distance)}</div>
                <div className="text-sm text-gray-500">Distance</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold">{formatDuration(routeStats.duration)}</div>
                <div className="text-sm text-gray-500">Duration</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold">{Math.round(routeStats.elevationGain)}m</div>
                <div className="text-sm text-gray-500">Elevation Gain</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold">{activityType === "run" ? pace[0].toFixed(2) : speed[0].toFixed(1)}</div>
                <div className="text-sm text-gray-500">{activityType === "run" ? "min/km" : "km/h"}</div>
              </div>
            </div>
          </div>

          <div>
            <label className="text-sm font-medium">{activityType === "run" ? "Pace (min/km)" : "Speed (km/h)"}</label>
            {activityType === "run" ? (
              <Slider
                value={pace}
                onValueChange={handlePaceChange}
                max={10}
                min={3}
                step={0.1}
                className="mt-2"
              />
            ) : (
              <Slider
                value={speed}
                onValueChange={handleSpeedChange}
                max={60}
                min={5}
                step={0.5}
                className="mt-2"
              />
            )}
          </div>

          <div>
            <label className="text-sm font-medium">{activityType === "run" ? "Run Name" : "Ride Name"}</label>
            <Input placeholder={activityType === "run" ? "Morning Run" : "Morning Ride"} className="mt-1" />
          </div>

          <div>
            <label className="text-sm font-medium">Date</label>
            <Popover>
              <PopoverTrigger asChild>
                <Button variant="outline" className="w-full justify-start text-left font-normal mt-1">
                  {date ? date.toLocaleDateString() : "Pick a date"}
                </Button>
              </PopoverTrigger>
              <PopoverContent className="w-auto p-0">
                <Calendar
                  mode="single"
                  selected={date}
                  onSelect={setDate}
                />
              </PopoverContent>
            </Popover>
          </div>

          <div>
            <label className="text-sm font-medium">Start Time</label>
            <div className="flex gap-2 mt-1">
              <Select defaultValue="07">
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {Array.from({length: 24}).map((_, i) => (
                    <SelectItem key={i} value={i.toString().padStart(2, "0")}>
                      {i.toString().padStart(2, "0")}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <span className="text-xl">:</span>
              <Select defaultValue="00">
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {Array.from({length: 60}).map((_, i) => (
                    <SelectItem key={i} value={i.toString().padStart(2, "0")}>
                      {i.toString().padStart(2, "0")}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>

          <div>
            <label className="text-sm font-medium">Description</label>
            <Textarea 
              placeholder={activityType === "run" ? "Great morning run through the park..." : "Great morning ride through the park..."}
              className="mt-1"
            />
          </div>

          <Button className="w-full bg-orange-500 hover:bg-orange-600">
            Download {activityType === "run" ? "Run" : "Ride"} File (1 Token)
          </Button>
        </div>
      </Card>
    </main>
  )
}
